<template>
  <div class="ParsingExcel" @click.stop="">
    <input
      ref="excel-upload-input"
      class="ParsingExcel-upload-input"
      type="file"
      accept=".xlsx, .xls"
      @change="handleClick"
    />
    <div
      class="ParsingExcel-upload-drop"
      flex="main:center cross:center"
      @drop="handleDrop"
      @dragover="handleDragover"
      @dragenter="handleDragover"
    >
      Excel
      <BLButton
        :loading="loading"
        class="dk-ml-10"
        type="primary"
        @click="handleUpload"
      >
        
      </BLButton>
    </div>

    <BLModal
      width="90%"
      :visible="dialogVisible"
      :ok-button-props="{ htmlType: 'submit' }"
      @cancel="dialogVisible = false"
    >
      <div slot="title">
        {{ myDialogTitle }}
        <span class="dk-content">
          :<span class="excelFileColor">{{ excelFileName }}</span
          >; :<span class="excelFileColor">{{ excelFileSize }}</span>
        </span>
      </div>
      <div class="scroll-div-box1">
        <AgGridTable
          :ag-table-options="agTableOptions"
          :ag-table-switch="{
            isScrollEndRequest: false,
            isShowExcelDownload: false,
            drawerDoubleShow: false,
            isShowPagination: false,
            isShowExcelUpload: false,
          }"
          :query-params="agQueryParams"
          @getGridApi="getGridApi"
        />
      </div>
      <div slot="footer">
        <BLButton @click="dialogVisible = false"> </BLButton>
        <BLButton type="primary" @click="dialogVisible = false"> </BLButton>
      </div>
    </BLModal>
  </div>
</template>

<script>
import XLSX from "xlsx";

// import AgGridTableMixins from "@/components/AgGridTable/agTableMixins/index";
import moment from "moment";
import {
  InitColumnDefs,
  AgGridUtils,
} from "@/components/AgGridTable/common/agGrid-utils";

export default {
  name: "ParsingExcel",
  // mixins: [AgGridTableMixins],
  props: {
    // excel,  10M
    excelLimitSize: {
      type: Number,
      default: 50,
    },
    // beforeUpload: Function, // eslint-disable-line
    // onSuccess: Function // eslint-disable-line
  },
  data() {
    return {
      agQueryParams: {
        // truefalsev-if
        initTableSuccess: true,
        // loading,truefalse
        getDataLoading: true,
        // 
        totalPages: 0,
        // 
        total: 0,
        // 
        pageNum: 1,
        // 
        pageSize: 10000,
        // 
        filterChangedObj: {},
        // BLHeaderSearch
        queryParam: {},
        // BLHeaderSearch/
        toggleSearchStatus: false,
      },

      agTableOptions: {
        //  alpine / balham / material
        theme: "alpine",
        // style
        style: null,
        // api
        agTableApi: null,
        // 
        initColumnDefs: null,
        columnDefs: [],
        rowData: [],
      },

      fieldsConfig: {
        id: "AgGridParsingExcel",
        name: "AgGridexcel",
        isRequestServerFields: false,
        database: {},
      },

      // loading
      loading: false,
      // exceltable
      dialogVisible: false,
      // excel
      excelData: {
        header: null,
        results: null,
      },
      // 
      parsingExcelStartTime: "",
      // 
      myDialogTitle: "Excel",
      // excel
      excelFileName: "",
      // excel
      excelFileSize: "",
    };
  },
  methods: {
    // ag-gridapi
    getGridApi(ele) {
      const agTableApi = new AgGridUtils(ele, this.fieldsConfig);
      console.log("agTableApi======>", agTableApi);
      this.agTableOptions.agTableApi = agTableApi;
    },
    // 
    beforeUpload(file) {
      const name = file.name;
      const sizeToM = file.size / 1024 / 1024;
      const convertM = `${sizeToM.toFixed(2)}M`;

      const alertMsg = (message, type = "info") => {
        this.$message.open({
          content: message,
          key: this.fieldsConfig.id,
          type,
          duration: 5000,
        });
      };

      this.excelFileName = name;
      this.excelFileSize = convertM;

      const isOverLimitSize = sizeToM < this.excelLimitSize;

      if (isOverLimitSize) {
        if (sizeToM > 0 && sizeToM <= 5) {
          alertMsg(`${name}${convertM}0~2`);
          return true;
        } else if (sizeToM > 5 && sizeToM <= 20) {
          alertMsg(`${name}${convertM}2~6`);
          return true;
        } else if (sizeToM > 20 && sizeToM <= 50) {
          alertMsg(
            `${name}${convertM}6~10`
          );
          return true;
        } else {
          alertMsg(
            `${name}${convertM}`,
            "warning"
          );
          return false;
        }
      } else {
        alertMsg(
          `${name}${convertM}M,${this.excelLimitSize}M`,
          "warning"
        );
        return false;
      }
    },
    async onSuccess({ results, header }) {
      console.log("results", results);
      console.log("header", header);

      this.agQueryParams.initTableSuccess = false;
      this.agQueryParams.getDataLoading = true;
      const database = {};

      for (let i = 0; i < header.length; i++) {
        database[header[i]] = header[i];
      }

      this.fieldsConfig.database = database;
      const initColumnDefs = new InitColumnDefs();
      const currentFields = await initColumnDefs.judgeReadServerFieldsData(
        this.fieldsConfig
      );

      console.log("initColumnDefs123123123", initColumnDefs);
      this.agTableOptions.initColumnDefs = initColumnDefs;
      this.agTableOptions.columnDefs = currentFields;
      this.agTableOptions.rowData = results;
      this.agQueryParams.initTableSuccess = true;
      this.agQueryParams.getDataLoading = false;
      // 
      this.diffStartTimeToEndTime();
      this.$message.success({
        content: "",
        key: this.fieldsConfig.id,
        duration: 2,
      });
      this.dialogVisible = true;
    },
    // 
    diffStartTimeToEndTime() {
      const parsingExcelEndTime = moment();
      const diffTime = parsingExcelEndTime.to(this.parsingExcelStartTime);
      console.log("diffTime=======>", diffTime);
    },

    // wxcel ,
    generateData({ header, results }) {
      this.excelData.header = header;
      this.excelData.results = results;
      this.onSuccess && this.onSuccess(this.excelData);
    },
    // 
    handleDrop(e) {
      e.stopPropagation();
      e.preventDefault();
      if (this.loading) return;
      const files = e.dataTransfer.files;
      if (files.length !== 1) {
        this.$message.error("1");
        return;
      }
      const rawFile = files[0]; // only use files[0]

      if (!this.isExcel(rawFile)) {
        this.$message.error(".xlsx, .xls, .csvExcel");
        return false;
      }
      this.upload(rawFile);
      e.stopPropagation();
      e.preventDefault();
    },
    // 
    handleDragover(e) {
      e.stopPropagation();
      e.preventDefault();
      e.dataTransfer.dropEffect = "copy";
    },
    // 
    handleUpload() {
      this.$nextTick(() => {
        this.$refs["excel-upload-input"]?.click();
      });
    },
    // 
    handleClick(e) {
      console.log("======>", e);
      const files = e.target.files;
      const rawFile = files[0]; // only use files[0]
      if (!rawFile) return;
      this.upload(rawFile);
    },
    // 
    upload(rawFile) {
      this.$nextTick(() => {
        this.$refs["excel-upload-input"].value = null; // fix can't select the same excel
        if (!this.beforeUpload) {
          this.readerData(rawFile);
          return;
        }
        const before = this.beforeUpload(rawFile);
        if (before) {
          this.getExcelFileInfo(rawFile);
        }
      });
    },
    // excel
    getExcelFileInfo(rawFile) {
      console.log("rawFile========>", rawFile);
      this.parsingExcelStartTime = moment();
      console.log("parsingExcelStartTime", this.parsingExcelStartTime);
      this.readerData(rawFile);
    },
    // 
    readerData(rawFile) {
      this.loading = true;
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = (e) => {
          const data = e.target.result;
          const workbook = XLSX.read(data, { type: "array" });
          const firstSheetName = workbook.SheetNames[0];
          const worksheet = workbook.Sheets[firstSheetName];
          const header = this.getHeaderRow(worksheet);
          const results = XLSX.utils.sheet_to_json(worksheet);
          this.generateData({ header, results });
          this.loading = false;
          resolve(true);
        };
        reader.readAsArrayBuffer(rawFile);
      });
    },
    // 
    getHeaderRow(sheet) {
      const headers = [];
      const range = XLSX.utils.decode_range(sheet["!ref"]);
      let C;
      const R = range.s.r;
      /* start in the first row */
      for (C = range.s.c; C <= range.e.c; ++C) {
        /* walk every column in the range */
        const cell = sheet[XLSX.utils.encode_cell({ c: C, r: R })];
        /* find the cell in the first row */
        let hdr = "UNKNOWN " + C; // <-- replace with your desired default
        if (cell && cell.t) hdr = XLSX.utils.format_cell(cell);
        headers.push(hdr);
      }
      return headers;
    },
    // excel
    isExcel(file) {
      return /\.(xlsx|xls|csv)$/.test(file.name);
    },
  },
};
</script>

<style scoped lang="less">
.ParsingExcel-upload {
  &-input {
    display: none;
    z-index: -9999;
  }
  &-drop {
    border: 2px dashed #bbb;
    width: 600px;
    height: 160px;
    margin: 0 auto;
    font-size: 24px;
    border-radius: 5px;
    text-align: center;
    color: #bbb;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.excelFileColor {
  color: @primary-color;
}
</style>
