// 
export const AG_GRID_EDIT_CELL_OBJ = {
  editable: params => {
    // 
    if (params.node.rowPinned === 'bottom') return false
    return true
  },
  cellStyle: params => {
    if (params.node.rowPinned === 'bottom') {
      return {
        backgroundColor: '#fff',
        color: '#333'
      }
    } else {
      return {
        backgroundColor: '#E1FFFF',
        color: 'red'
      }
    }
  }
}

