import { message } from "ant-design-vue";

// 
export const httpConfig = {
  isDevEnv: true, // 
  cancelRepeatRequest: true, // 
  retry: 3, // retry 
  retryDelay: 1000, // retryDelay 
  cache: true, // cache true 
  setExpireTime: 30000, // 
  messageDuration: 3 * 1000, // 
  timeout: 2 * 60 * 1000, // 
  baseURL: process.env.VUE_APP_API_BASE_URL, // 
  textBaseUrl: process.env.VUE_APP_API_BASE_URL, // 
};

/**
 * @description: 
 * @param { Object } error 
 * @return {*}
 */
export function addErrorLog(error) {
  console.log("error", error);
}

/**
 * @description: 
 * @param { String } message 
 * @return {*}
 */
export function messageAlert(content = "") {
  console.log("", content);
  message.error(content);
}

/**
 * @description 
 * @param {Error} error 
 */
export function handleError(error) {
  // 
  addErrorLog(error);
  // 
  if (httpConfig.isDevEnv) {
    console.error(">>>>>> Error >>>>>>");
    console.log(error);
  }
  setTimeout(() => {
    // 
    messageAlert(error.message);
  });
}

/**
 * @description: JSON
 * @param {*} str 
 * @return {*}
 */
export function isJsonStr(str) {
  if (typeof str === "string") {
    try {
      var obj = JSON.parse(str);
      if (typeof obj === "object" && obj) {
        return true;
      } else {
        return false;
      }
    } catch (e) {
      console.log("error" + str + "!!!" + e);
      return false;
    }
  }
}

/**
 * @param value
 * @returns {string}  
 *  isType(func) !== 'Function'
 */
export function isType(value) {
  return Object.prototype.toString.call(value).slice(8, -1);
}
