import JsPDF from "jspdf";

/* 
jsPDF  PDF  JavaScript  jsPDF 

jsPDF('1', 'pt', [width, height]):  PDF  '1'  A4 l:p:'pt'  pointwidth  height 
pdf.setFont(font, size):  PDF  font  'Helvetica'  'Times'size 
pdf.setLineWidth(width):  PDF  width 
pdf.drawLine(x1, y1, x2, y2):  PDF  x1y1  x2y2 
pdf.drawRect(x, y, width, height):  PDF  xy width  height 
pdf.drawText(text, x, y):  PDF  text x  y 
pdf.save('filename.pdf'):  PDF  'filename.pdf'  PDF 
pdf.addImage(imageData, type, x, y, width, height):  PDF  imageData type  'JPEG'  'PNG'xy width  height 
pdf.rotate(angle, x, y):  PDF  angle x  y 
pdf.translate(x, y):  PDF  x  y 
*/


export function exportPDFFile(text) {
  const doc = new JsPDF();

  doc.text(text, 10, 10);
  doc.save("a4.pdf");
}
