import Axios from "axios";
import { get } from "lodash";
import * as storage from "@/utils/storage";
import store from "@/store";
import router from "@/router/index";

// 
import { httpConfig, handleError, messageAlert } from "./httpCommon";
// 
const { isDevEnv, baseURL, textBaseUrl, timeout } = httpConfig;

const http = Axios.create({
  timeout,
  // `withCredentials` storage
  withCredentials: false, // default
});

if (isDevEnv) {
  http.defaults.baseURL = textBaseUrl;
} else {
  http.defaults.baseURL = baseURL;
}

// 
http.interceptors.request.use(
  (config) => {
    const Authorization = storage.getStorage("token");
    if (Authorization) {
      config.headers["Authorization"] = `Bearer ${Authorization}`;
    }

    return config;
  },
  (error) => {
    console.log(error);
    return Promise.reject(error);
  }
);

// 
http.interceptors.response.use(
  async (response) => {
    if (response.status === 200) {
      const resData = response.data;
      if (resData.code === 200) {
        return resData;
      } else if (resData.code === 401) {
        await store.dispatch("user/logout");
        router.push({
          path: "/login",
        });
        return resData.data;
      } else {
        console.log('response.config', response.config)
        let responseType = response.config?.responseType
        if (responseType !== 'blob') {
          messageAlert(`${resData.message || ""}`);
        }
        return resData
      }
    }
  },
  (error) => {
    console.log("error=====>", error);

    // 
    errorStatusMsg(error);

    return Promise.reject(error);
  }
);

/**
 * @description: 
 * @param { object } error 
 * @return { }
 */
function errorStatusMsg(error) {
  const status = get(error, "response.status");
  switch (status) {
    case 302:
      error.message = "";
      break;
    case 400:
      error.message = "";
      break;
    case 401:
      error.message = "";
      break;
    case 403:
      error.message = ",";
      break;
    case 404:
      error.message = `: ${error.response.config.url}`;
      break;
    case 408:
      error.message = "";
      break;
    case 409:
      error.message = "";
      break;
    case 500:
      error.message = "";
      break;
    case 501:
      error.message = "";
      break;
    case 502:
      error.message = "";
      break;
    case 503:
      error.message = "";
      break;
    case 504:
      error.message = "";
      break;
    case 505:
      error.message = "HTTP";
      break;
    default:
      break;
  }
  if (error.message.includes("timeout")) error.message = "";
  if (error.message.includes("Network")) {
    error.message = window.navigator.onLine ? "" : "";
  }

  // 
  handleError(error);
}

export default http;
