/* 
 html2canvas  HTML  HTML  Canvashtml2canvas 

html2canvas(element, options);


element  canvas  HTML  DOM 
options  html2canvas 



allowTaint: false  false truehtml2canvas 
backgroundColor: #ffffff canvas 
useCORS: false  CORSCross-Origin Resource Sharing true html2canvas  CORS 
logging: false 
width  height canvas 
scale: window.devicePixelRatio  canvas 

*/

import html2Canvas from "html2canvas";

import JsPDF from "jspdf";

/**
 * @description: pdf
 * @param {string} title 
 * @param {HTMLElement} html ldocument.querySelector('#myPrintHtml')
 * @return {*}
 */
export function htmlPdf(title, html) {
  html2Canvas(html, {
    allowTaint: true,
    useCORS: true,
    dpi: window.devicePixelRatio * 4, // DPI 
    background: "#FFFFFF",
  }).then((canvas) => {
    //pdfhtml
    var leftHeight = canvas.height;

    var a4Width = 595.28;
    //A4210mm*297mm10mm190x277 //pdfhtmlcanvas;
    var a4Height = 841.89;
    var a4HeightRef = Math.floor((canvas.width / a4Width) * a4Height); //pdf

    var position = 0;

    var pageData = canvas.toDataURL("image/jpeg", 1.0);

    var pdf = new JsPDF("p", "pt", "a4"); //A4
    var index = 1,
      canvas1 = document.createElement("canvas"),
      height;
    pdf.setDisplayMode("fullwidth", "continuous", "FullScreen");

    // var pdfName = title;
    function createImpl(canvas) {
      console.log(leftHeight, a4HeightRef);
      if (leftHeight > 0) {
        index++;

        var checkCount = 0;
        if (leftHeight > a4HeightRef) {
          var i = position + a4HeightRef;
          for (i = position + a4HeightRef; i >= position; i--) {
            var isWrite = true;
            for (var j = 0; j < canvas.width; j++) {
              var c = canvas.getContext("2d").getImageData(j, i, 1, 1).data;

              if (c[0] != 0xff || c[1] != 0xff || c[2] != 0xff) {
                isWrite = false;
                break;
              }
            }
            if (isWrite) {
              checkCount++;
              if (checkCount >= 10) {
                break;
              }
            } else {
              checkCount = 0;
            }
          }
          height =
            Math.round(i - position) || Math.min(leftHeight, a4HeightRef);
          if (height <= 0) {
            height = a4HeightRef;
          }
        } else {
          height = leftHeight;
        }

        canvas1.width = canvas.width;
        canvas1.height = height;

        console.log(index, "height:", height, "pos", position);

        var ctx = canvas1.getContext("2d");
        ctx.drawImage(
          canvas,
          0,
          position,
          canvas.width,
          height,
          0,
          0,
          canvas.width,
          height
        );

        // var pageHeight = Math.round((a4Width / canvas.width) * height);
        // pdf.setPageSize(null,pageHeight)

        if (position != 0) {
          pdf.addPage();
        }
        pdf.addImage(
          canvas1.toDataURL("image/jpeg", 1.0),
          "JPEG",
          10,
          10,
          a4Width,
          (a4Width / canvas1.width) * height
        );
        leftHeight -= height;
        position += height;
        if (leftHeight > 0) {
          setTimeout(createImpl, 500, canvas);
        } else {
          pdf.save(title + ".pdf");
        }
      }
    } //pdf

    if (leftHeight < a4HeightRef) {
      pdf.addImage(
        pageData,
        "JPEG",
        0,
        0,
        a4Width,
        (a4Width / canvas.width) * leftHeight
      );
      pdf.save(title + ".pdf");
    } else {
      try {
        pdf.deletePage(0);
        setTimeout(createImpl, 500, canvas);
      } catch (err) {
        console.log(err);
      }
    }
  });
}

export default htmlPdf;
