/**
 * @description: 
 * @param {*} rule
 * @param {*} value
 * @param {*} callback
 * @return {*}
 */
export function isEmail(rule, value, callback) {
  const regStr =
    /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  if (regStr.test(value)) {
    callback();
  } else {
    callback("");
  }
}

/**
 * @description: 
 * @param {*} rule
 * @param {*} value
 * @param {*} callback
 * @return {*}
 */
export function isPhoneNumber(rule, value, callback) {
  const regStr = /^(?:(?:\+|00)86)?1\d{10}$/;
  if (regStr.test(value)) {
    callback();
  } else {
    callback("");
  }
}

/**
 * @description: 
 * @param {*} rule
 * @param {*} value
 * @param {*} callback
 * @return {*}
 */
export function isUserName(rule, value, callback) {
  const regStr = /^[a-zA-Z0-9!@#$%^&*?_-]{6,16}$/;
  if (regStr.test(value)) {
    callback();
  } else {
    callback("616!@#$%^&*?_-");
  }
}

/**
 * @description: 
 * @param {*} rule
 * @param {*} value
 * @param {*} callback
 * @return {*}
 */
export function isChinese(rule, value, callback) {
  const regStr = /^[\u4e00-\u9fa5]+$/gi.test(value)
  if (regStr.test(value)) {
    callback();
  } else {
    callback("2-4");
  }
}


/**
 * @description: 
 * @param {*} rule
 * @param {*} value
 * @param {*} callback
 * @return {*}
 */
export function isNumberverifyCode(rule, value, callback) {
  const regStr = /^[0-9]{6}$/;
  if (regStr.test(value)) {
    callback();
  } else {
    callback("6");
  }
}

/**
 * @description: 6
 * @param {*} rule
 * @param {*} value
 * @param {*} callback
 * @return {*}
 */
export function isSixVifyCode(rule, value, callback) {
  const regStr = /^[0-9A-Za-z]{6}$/;
  if (regStr.test(value)) {
    callback();
  } else {
    callback("6/");
  }
}

/**
 * @description: 
 * @param {*} rule
 * @param {*} value
 * @param {*} callback
 * @return {*}
 */
export function isIDCard(rule, value, callback) {
  const regStr = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/
  if (regStr.test(value)) {
    callback();
  } else {
    callback("");
  }
}


/**
 * @description: 
 * @param {*} rule
 * @param {*} value
 * @param {*} callback
 * @return {*}
 */
export function isPassword(rule, value, callback) {
  const regStr = /[a-zA-Z0-9!@#$%^&*?]{6,16}$/;
  if (regStr.test(value)) {
    callback();
  } else {
    callback("616");
  }
}
