import { message as Message } from "ant-design-vue";

export const EXCEL = {
  /**
   * @description: Excel
   * @param {string[]} tHeader ['', '', '']
   * @param {object[]} data  
   * @param {string[]} lastRow  
   * @param {string} title  
   */
  exportExcel: function (dataObj) {
    const { tHeader, data, lastRow, title } = dataObj;
    return new Promise((resolve) => {
      require.ensure([], () => {
        const { export_json_to_excel } = require("./Export2Excel"); // Export2Excel
        data.push(...lastRow); // 
        export_json_to_excel(tHeader, data, title); // 
        resolve(true);
      });
    });
  },

  /**
   * @description: Excel
   * @param {string[]} filterVal 
   * @param {object[]} jsonData 
   * @return {} 
   */
  formatJson: function (filterVal, jsonData) {
    return jsonData.map((v) => filterVal.map((j) => v[j]));
  },

  /**
   * @description: Excel 
   * @param {string[]} filterVal 
   * @param {object[]} jsonData 
   * @return {} 
   */
  formatJson1: function (filterVal, jsonData) {
    const formatExcelData = (str) => {
      if (str) {
        if (/[Fee|Money|Price|Pay|Weight|Volume]$/g.test(str)) {
          const val = Number(str);
          // excel11' 
          if (isNaN(val)) {
            return str;
          } else {
            if (val.toString().length > 10) {
              return `'${val}`;
            } else {
              return val;
            }
          }
        } else {
          return str;
        }
      } else {
        return str;
      }
    };
    return jsonData.map((v) =>
      filterVal.map((j) => {
        const str = v[j];
        return formatExcelData(str);
      })
    );
  },

  /**
   * @description: excel
   * @param {string[]} tHeader   string[] , ['', '']
   * @param {string[]} filterVal string[] , ['time', 'number']
   * @param {object[] } tableData 
   * @param {string} title 
   * @param {any[]} lastRow  string[] 
   * @return {*}
   */

  exportExcelFun: function (data) {
    // console.log('excel', data)
    const that = this;
    // list
    if (Array.isArray(data.tableData)) {
      const dataObj = {
        tHeader: data.tHeader,
        filterVal: data.filterVal,
        lastRow: data.lastRow || [],
        title: data.title || "downLoadExcel",
        data: function () {
          // this
          return that.formatJson1(data.filterVal, data.tableData);
        },
      };
      // console.log('dataObj.data()', dataObj.data())
      dataObj.data = dataObj.data(); // 
      that.exportExcel(dataObj).then((res) => {
        if (res) {
          // console.log('Excel')
          Message.success(`${dataObj.title}Excel`);
        } else {
          Message.error(`${dataObj.title}Excel`);
        }
      });
    } else {
      Message.warning("");
    }
  },

  /* 
  https://juejin.cn/post/7282950051319595066?searchId=202405061028396619942978A84BCD82DB
  Excel

  1multiHeadermultiHeader2filterValtableDatatitleindexNumberExcel
  2EXPORT_FILENAMEstyle1style2Excel
  3importExport2Excel.jsExcelexport_json_to_excelhb
  4export_json_to_excelhb
  */

  /**
   * @description: StyleExcel
   * @param {string[]} multiHeader 1
   * @param {string[]} multiHeader 2
   * @param {string[]} filterVal 
   * @param {object[]} tableData 
   * @param {string} title 
   * @param {number} indexNumber 
   * @param {number} num2 , 1
   * @return {*}
   */
  exportStyleExcel(config) {
    const {
      multiHeader,
      multiHeader2,
      filterVal,
      tableData,
      title,
      indexNumber,
      num2 = 1,
    } = config;

    console.log("config123123123", config);

    // 
    const EXPORT_FILENAME = title;
    // 1 dbf3f2
    const style1 = {
      fill: { patternType: "solid", fgColor: { rgb: "dbf3f2" } },
      border: {
        top: { style: "thin" },
        left: { style: "thin" },
        bottom: { style: "thin" },
        right: { style: "thin" },
      },
      alignment: { horizontal: "center", vertical: "center", wrapText: 1 },
    };
    // 2 fbedd7
    const style2 = {
      fill: { patternType: "solid", fgColor: { rgb: "fbedd7" } },
      border: {
        top: { style: "thin" },
        left: { style: "thin" },
        bottom: { style: "thin" },
        right: { style: "thin" },
      },
      alignment: { horizontal: "center", vertical: "center", wrapText: 1 },
    };
    // 
    let headerList = [];
    let titleList = [];
    let styleNumber = 1; // 1 1
    let headerStyle = style2;
    let rowIndex = 1;
    let colIndex = 1;
    let isOdd = true;
    let count = 0;
    const indexNumber2 = indexNumber + 1;
    // 
    if (multiHeader && multiHeader.length > 0) {
      multiHeader.forEach(() => {
        if (rowIndex < multiHeader.length) {
          headerList.push({
            s: { r: 0, c: rowIndex },
            e: { r: 0, c: rowIndex + indexNumber },
            style: headerStyle,
          });
          rowIndex += indexNumber2;
        }
        // 
        if (styleNumber === 1) {
          styleNumber = 2; // 
          headerStyle = style1;
        } else {
          styleNumber = 1; // 
          headerStyle = style2;
        }
      });
    }
    if (multiHeader2 && multiHeader2.length > 0) {
      multiHeader2.forEach(() => {
        if (colIndex < multiHeader2.length) {
          titleList.push({
            s: { r: 1, c: colIndex },
            e: { r: 1, c: colIndex },
            style: isOdd ? style2 : style1,
          });
          colIndex += 1;
          count++;
        }
        if (count % indexNumber2 === 0) {
          isOdd = !isOdd;
        }
      });
    }
    //  num2 
    const merges = [
      // A1A21
      {
        s: { r: 0, c: 0 },
        e: { r: num2, c: 0 },
        style: {
          fill: { patternType: "solid", fgColor: { rgb: "d4e6fd" } },
          border: {
            top: { style: "thin" },
            left: { style: "thin" },
            bottom: { style: "thin" },
            right: { style: "thin" },
          },
          alignment: { horizontal: "center", vertical: "center", wrapText: 1 },
        },
      },
      ...headerList,
      ...titleList, // 
    ];
    const data = tableData.map((v) => filterVal.map((j) => v[j]));

    return new Promise((resolve) => {
      require.ensure([], () => {
        const { export_json_to_excelhb } = require("./Export2StyleExcel"); // Export2Excel
        export_json_to_excelhb({
          multiHeader: [multiHeader], // 
          multiHeader2: [multiHeader2], // 
          data,
          filename: EXPORT_FILENAME,
          merges,
        }); // 
        resolve(true);
      });
    });
  },
};
