import { saveAs } from "file-saver";

/**
 * @description: json
 * @param {String} data 
 * @param {String} fileName 
 * @return {*}
 */
export function downLoadJsonFile(data, fileName = "") {
  var blob = new Blob([JSON.stringify(data)], {
    type: "text/plain;charset=utf-8",
  });
  saveAs(blob, `${fileName}.json`);
}
