/**
 * @description: header 
 * @param { Object[] } header width , hide    [{ field:'field1' , headerName:'1' }]
 * @param { Object[] } cellStyleArr dom   
 * 
 *       [
          {
            headName: '',
            cellRender: cellRender // dom
          },
          {
            headName: '',
            cellRender: params => (Number(params.value) === 0 ? '' : '') // 01
          },
          {
            headName: '',
            filter: 'agDateColumnFilter' // filter
          },
          {
            headName: '',
              colSpan: function (params) {
               const country = params.data.country;
                if (country === 'Russia') {
                   // have all Russia age columns width 2
                   return 2;
                 } else if (country === 'United States') {
                   // have all United States column width 4
                   return 4;
                 } else {
                   // all other rows should be just normal
                   return 1;
                 }
               }
              }
         ],
 *
 * @return {*}
 */
export function batchProcessHeader(header = [], cellStyleArr = []) {
  return header.map((ele) => {
    const { headName } = ele;
    // width
    const getStrLen = (str = "") => {
      const len = str.length;
      const pxNum = len * 15 + 90;
      return pxNum;
    };

    let extraAttribute = {};

    cellStyleArr.forEach((el) => {
      if (el.headName === headName) {
        extraAttribute = { ...el };
      }
    });
    return {
      hide: false,
      width: getStrLen(headName),
      // ele
      ...ele,
      ...extraAttribute,
    };
  });
}

/**  ag-grid
 * @description: ,
 * @param { Object[] } headerData  ,   [{ field:'field1' , headerName:'1' ,hide:false }]
 * @param { Object[] } customerColumns 
 * @param { Object[] } pinnedLeftColumns 
 * @param { Object[] } pinnedRightColumns 
 * @param { Object[] } columnsDefaultOptions   showSerialColumns true   , showOperationColumns , true 
 * @return {*}
 */
export function setColumnDefs(
  headerData = [],
  columns = {
    pinnedLeftColumns: [],
    pinnedRightColumns: [],
    customerColumns: [],
  },
  columnsDefaultOptions = {
    showSerialColumns: true,
    showOperationColumns: true,
  }
) {
  // 
  if (columns.pinnedLeftColumns.length) {
    headerData.unshift(columns.pinnedLeftColumns);
  }

  // 
  if (columnsDefaultOptions.showSerialColumns) {
    // 
    const serialColumns = {
      headerName: "",
      field: "numericalOrder",
      width: 120,
      pinned: "left", // 
      lockPosition: true, // false,truepinned: 'right', 
      checkboxSelection: true, // 
      sortable: false,
      filter: false,
      headerCheckboxSelection: true,
      headerCheckboxSelectionFilteredOnly: true,
    };
    headerData.unshift(serialColumns);
  }

  // 
  if (columns.customerColumns.length) {
    console.log("", columns.customerColumns);
    headerData.push(columns.customerColumns);
  }

  if (columns.pinnedRightColumns.length) {
    headerData.push(columns.pinnedRightColumns);
  }

  // 
  if (columnsDefaultOptions.showOperationColumns) {
    // 
    const operationColumns = {
      headerName: "",
      field: "agTableOperation",
      pinned: "right", // 
      lockPosition: true,
      sortable: false,
      filter: false,
      width: 200,
    };
    headerData.push(operationColumns);
  }

  return headerData;
}

