import Vue from 'vue'
import Loading from './MyLoading.vue'
/**
 * Vue.extend new 
 *  new Mask()  div 
 **/
const Mask = Vue.extend(Loading)
//   myLoadingType: 'default'

// 
const toggleLoading = (el, binding) => {
  if (binding.value) {
    Vue.nextTick(() => {
      // loading
      el.instance.visible = true
      el.style.position = 'relative'
      // 
      insertDom(el, el)
    })
  } else {
    el.instance.visible = false
    el.style.position = 'static'
    el.mask && el.mask.parentNode && el.mask.parentNode.removeChild(el.mask)
  }
}

// 
const insertDom = (parent, el) => {
  parent.appendChild(el.mask)
}

export default {
  // 
  bind: function (el, binding) {
    const { modifiers } = binding
    let myLoadingType = 'default'
    if (modifiers) {
      const arr = Object.keys(modifiers)
      if (arr.length) {
        console.log('myLoading ======>', modifiers)
        myLoadingType = ['bounce'].includes(arr[0]) ? arr[0] : 'default'
      }
    }
    const mask = new Mask({
      el: document.createElement('div'),
      data () {
        return {
          myLoadingType: myLoadingType
        }
      }

    })
    // console.log('mask====>', mask)
    // mask
    el.instance = mask
    el.mask = mask.$el
    el.maskStyle = {}
    binding.value && toggleLoading(el, binding)
  },
  //  VNode --
  update: function (el, binding) {
    if (binding.oldValue !== binding.value) {
      toggleLoading(el, binding)
    }
  },
  // 
  unbind: function (el) {
    el.instance && el.instance.$destroy()
  }
}
